import React from 'react';
import './About.scss';

export default function About (){
    return(
        <section className="About" id="about">
            <div className="wrapper">
                <div className="section__title">About</div>
                <div className="section__content">
                    <p>A Scottish born and raised music director, based between the U.K and the U.S.</p>
                    <p>Studied at The Royal Academy of Music, London.</p>
                    <p>Before that, a Bachelor of Laws (LL.B), The University of Dundee in Scotland.</p>
                    <p>Alongside current projects, a part time student, Global Master of Business Administration, The University of London.</p>
                </div>
            </div>
        </section>
    )
};
